import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './DictVMUtil'
import * as itemUtil from '../base/BaseVMUtil'

import * as commandType from '../../user/command/base/CommandType'

import * as genUtil from '../../../../../util/generalUtil'
import * as commandUtil from '../../../../../util/commandUtil'

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable,
      CurrentItem: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var q = this.ModelItem.AddItem();
    var qI = itemUtil.get(q, this);

    this.Items.push(qI)
    return qI;
  }

  CurrentItem = undefined

  // validate
  Validate = (level) => {
    return commandUtil.Validate(undefined, { type: commandType.content, level })
  }

  // view
  // table
  get ItemsView() {
    return this.Items?.map(x => x.TitleView)
  }

  get View() {
    return <div className='flex'>
      <div className='w-3/12 pr-7'>
        <div className='mb-3'>
          <span className='text-lg font-medium'>D n</span>
        </div>

        {/* Items */}
        <div className='flex'>
          {
          // this.Validate('edit') && 
          true ?
            <div className='mt-2 mr-2'>
              <Button type='primary' size='small'
                className='btn-success'
                onClick={this.AddItem}>+</Button>
            </div>
            : ''}

          <div className='flex-1'>
            {this.ItemsView}
          </div>
        </div>
      </div>

      {/* CurrentItem */}
      <div className='flex-1'>
        {this.CurrentItem?.ContentView}
      </div>
    </div>
  }
}