import { makeObservable, observable, action } from 'mobx';
import { PlusCircleOutlined } from '@ant-design/icons';
import * as util from './DictVMUtil'
import * as itemUtil from '../base/BaseVMUtil'
import View from './DictView'

import * as commandType from '../../../user/command/base/CommandType'

import * as genUtil from '../../../../../../util/generalUtil'
import * as commandUtil from '../../../../../../util/commandUtil'
import { Button } from 'antd';

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable,
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var q = this.ModelItem.AddItem();
    var qI = itemUtil.get(q, this);

    this.Items.push(qI)
    return qI;
  }

  // 
  get IsInDiscipline() {
    return this.ModelItem.IsInDiscipline
  }

  // sub
  get IsSub() {
    return this.ModelItem.IsSub
  }

  // validate
  Validate = (level) => {
    return commandUtil.Validate(undefined, { type: commandType.content, level })
  }

  // view
  // table
  get ItemsView() {
    return this.Items?.map(x => x.View)
  }

  get View() {
    return <View key={this.id} store={this} />
  }

  get TableView() {
    return <table className='table table-bordered table-fixed'>
      <thead className='bg-[#e9ecef]'>
        <tr>
          <th className='w-8'></th>

          {/* Status */}
          <th className='w-28'></th>

          <th className='w-full'>
            <div className='flex'>
              <div className='ml-4'>Task</div>
              {
                // this.Validate('edit') && 
                true ?
                  <div className='ml-2 -mt-1 cursor-pointer text-[#1890ff]'
                    onClick={this.AddItem}>
                    <PlusCircleOutlined />
                  </div>
                  : ''}
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        {this.ItemsView}
      </tbody>
    </table>
  }

  get SubView() {
    return this.ItemsView
  }
}