import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './BaseVMUtil'

import * as genUtil from '../../../../../../util/generalUtil'

export class BaseVM {
  constructor() {
    makeObservable(this, {
      is_ShowSub : observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // data
  fields
  get Fields() {
    if (!this.fields) {
      this.fields = util.GetFields(this)
    } return this.fields
  }

  GetField = (name) => {
    return genUtil.SingleFilter(this.Fields, x => x.Key === name);
  }

  // sub
  subDict
  get SubDict(){
    if (!this.subDict){
      this.subDict = util.GetSubDict(this)
    } return this.subDict
  }

  //
  is_ShowSub = undefined
  get Is_ShowSub(){
    if (this.is_ShowSub === undefined){
      this.is_ShowSub = this.ModelItem.Is_ShowSub
    } return this.is_ShowSub
  } set Is_ShowSub(v){
    this.is_ShowSub = v;
    this.ModelItem.Is_ShowSub = v

    this.Save();
  }

  // rank
  get Rank() {
    return util.GetRank(this)
  }

  // status
  status
  get Status(){
    if (!this.status){
      this.status = util.GetStatus(this)
    } return this.status
  }

  // css
  get Css() {
    // return this === this.Dict.CurrentItem ? 'bg-[#52c41a90] text-white font-medium' : ''
    return ''
  }

  // current
  SetCurrent = () => {
    var dict = this.Dict

    if (dict.CurrentItem === this) {
      dict.CurrentItem = undefined
    } else {
      dict.CurrentItem = this
    }
  }

  // save
  Save = (handle) => {
    this.ModelItem.Save(handle);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  // validate
  Validate = (level) => {
    return this.Dict.Validate(level)
  }

  // view
  get View() {
    return util.GetView(this)
  }

  get Indent_View() {
    return util.GetIndent_View(this)
  }

  get RowView(){
    return util.GetRowView(this)
  }
}