import { Dict } from "./Dict"
import { Base as Project } from '../../../project/base/Base'
import { Base as Task } from '../base/Base'
import * as itemUtil from '../base/BaseUtil'
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const get = (dict) => {
  var qI = new Dict();

  qI.Dict = dict

  return qI;
}

