import { url } from './Base_Url'
import { Base } from './Base'
import * as taskDictUtil from '../../task/base/dict/DictUtil'

import * as api from '../../../../../util/apiUtil'

// Instance
export const GetFromData = (data, dict) => {
  var qI = get(dict);

  qI._id = data._id
  qI.Name = data.name

  return qI;
}

export const get = (dict) => {
  var qI = new Base();

  qI.Dict = dict;
  
  return qI;
}

// Property
// task
export const GetTaskDict = (q) => {
  return taskDictUtil.get(q)
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}