import { makeObservable, observable, action } from 'mobx';
import * as util from './StatusVMUtil'
import * as ss from './StatusState'
import { message } from 'antd';

export class StatusVM {
  constructor() {
    makeObservable(this, {
      state: observable
    })
  }

  // state
  state = undefined
  get State() {
    if (!this.state) {
      this.state = this.ModelItem.State
    } return this.state
  } set State(v) {
    this.state = v
    this.ModelItem.State = v
  }

  get FullState() {
    var task = this.Dict
    var list = task.SubDict.Items;
    if (!list) return ss.draft;

    var length = list.length

    if (length === 0) return this.State

    var doneItems = list.filter(x => x.Status.FullState === ss.done)
    return length === doneItems.length ? ss.done : ss.draft
  }

  // add sub
  AddSub = () => {
    if (!(
      // this.Validate('edit') &&
      true)) return;

    var task = this.Dict
    var dict = task.SubDict
    dict.AddItem();

    task.Is_ShowSub = true;
  }

  // showsub
  get ShowSub_Css() {
    return `${this.Can_ShowSub_Css} ${this.Is_ShowSub_Css}`
  }

  get Can_ShowSub_Css() {
    var list = this.Dict.SubDict.Items
    return list && list.length !== 0 ? '' : 'invisible'
  }

  get Is_ShowSub_Css() {
    return this.Dict.Is_ShowSub ? 'text-[#52c41a]' : 'text-[#faad14]';
  }

  ToggleSub = () => {
    var dict = this.Dict;
    dict.Is_ShowSub = !dict.Is_ShowSub
  }

  // done task
  DoneTask_Message

  get IsValid_DoneTask() {
    var task = this.Dict;

    var list = task.SubDict.Items;
    if (!list) return false;

    if (list.length !== 0) {
      this.DoneTask_Message = 'Bn phi duyt cc cng vic con'
      return false;
    }

    this.DoneTask_Message = undefined
    return true;
  }

  get DoneTask_Css() {
    return this.FullState === ss.done ?
      'text-[#52c41a]' :
      this.IsValid_DoneTask ? 'text-[#faad14]' : 'text-gray-500'
  }

  DoneTask = () => {
    if (this.FullState === ss.done) return;

    if (this.DoneTask_Message) {
      message.warn(this.DoneTask_Message);
      return
    }

    this.State = ss.done

    var task = this.Dict
    task.Save();
  }

  // validate
  Validate = (level) => {
    return this.Dict.Validate(level)
  }

  // view
  get View() {
    return util.GetView(this)
  }
}