import { PlusCircleOutlined, SolutionOutlined, CheckCircleOutlined } from '@ant-design/icons';
import { StatusVM } from "./StatusVM"

// instance
export const get = (q, dict) => {
  var qI = new StatusVM();

  qI.ModelItem = q
  qI.Dict = dict;

  return qI;
}

// property
// view
export const GetView = (q) => {
  return <div className='flex'>
    <div className='flex'>
      <div className='cursor-pointer text-[#1890ff]'
        onClick={q.AddSub}>
        <PlusCircleOutlined />
      </div>
      <div className={`ml-2 cursor-pointer ${q.ShowSub_Css}`}
        onClick={q.ToggleSub}>
        <SolutionOutlined />
      </div>

      {q.Dict.DeleteVM.View}
    </div>

    <div className='flex ml-3'>
      <div className={`cursor-pointer ${q.DoneTask_Css}`}
        onClick={q.DoneTask}>
        <CheckCircleOutlined />
      </div>
    </div>
  </div>
}