import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';
import { url } from './Base_Url'
import * as util from './BaseUtil'

import * as genUtil from '../../../../../util/generalUtil'

export class Base {
  constructor() {
    makeObservable(this, {
      Name : observable
    })
  }

  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Name = 'd n'

  // task
  taskDict
  get TaskDict(){
    if (!this.taskDict){
      this.taskDict = util.GetTaskDict(this)
    } return this.taskDict
  }

  // apidata
  get ApiData() {
    var obj = {   
      _id: this._id,
      order : this.Index,
      name: this.Name,
    }

    return obj;
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  Save = (handle) => {
    const func = () => {
      if (handle){
        handle();
      }
    }

    const finish = () => {
      this.OnSave = false;
      func();
    }

    if (this.OnSave){
      func();
      return;
    } else {
      this.OnSave = true;
    }

    this.SaveDatabase(false, finish)
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(false)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage) {
        message.success('delete successfully!');
      }
      if (handle) {
        handle()
      }
    });
  }
}