import * as api from '../../../../util/apiUtil'
import { PMData } from '../model/PMData';
import { PMDataVM } from './PMDataVM';
import * as genUtil from '../../../../util/generalUtil'
import * as layoutUtil from '../../entity/layout/layout/LayoutUtil'

// user
import * as userDictUtil from '../../../user/dict/User_DictVMUtil'

// role
import * as roleDictUtil from '../../entity/user/role/dict/DictVMUtil'

// project
import * as projectDictUtil from '../../entity/project/dict/DictVMUtil'

// task
import * as taskDictUtil from '../../entity/task/base/dict/DictVMUtil'

export const pmData = () => PMData.Instance;

// Instance
export const get = () => {
  var qI = new PMDataVM();

  return qI;
}

// Property
// layout
export const GetLayout = (q) => {
  return layoutUtil.get()
}
// user
export const GetUserDict = (data) => {
  var qI = userDictUtil.get(pmData().UserDict, data);

  qI.ViewRender = () => <div className='ud__wrapper'>
    <div className='ud__dec f'>
      <div className='f1' />
      <div>
        <div className='ud__main__wrapper'>
          {qI.MainView}
        </div>
        {qI.ModalView}
      </div>
    </div>
  </div>

  return qI;
}

// role
export const GetRoleDict = (data) => {
  return roleDictUtil.get(pmData().RoleDict, data);
}

// project
export const GetProjectDict = (q) => {
  return projectDictUtil.get(pmData().ProjectDict, q)
}

// task
export const GetTaskDict = (q) => {
  return taskDictUtil.get(pmData().TaskDict, q);
}