import { PMData } from '../model/main/single/model/PMData';
import { PMDataVM } from '../model/main/single/form/PMDataVM';
import * as api from './apiUtil'

export let dtFormat = new Intl.DateTimeFormat('en-GB');

export const deepClone = (q) => {
  return JSON.parse(JSON.stringify(q));
}

// random
export const randomId = () => {
  // Math.random should be unique because of its seeding algorithm.
  // Convert it to base 36 (numbers + letters), and grab the first 9 characters
  // after the decimal.
  return Math.random().toString(36).substr(2, 9);
};

export const newGUID = () => {
  return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, c =>
    (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
  );
}

export const randomInt = (digitLength = 4) => {
  return Math.floor(Math.random() * Math.pow(10, digitLength))
}

// model
export const UpdateFromModel = (obj, action) => {
  obj.M2VM = true;
  action();
  obj.M2VM = false;
}

export const MoveItem = (list, oldIndex, newIndex) => {
  if (oldIndex === -1 || newIndex === -1 ||
    oldIndex === newIndex) return;
  var temp = list[oldIndex];
  list[oldIndex] = list[newIndex];
  list[newIndex] = temp;
}

export const IsEqual = (list1, list2) => {
  if (list1.length !== list2.length) return false;
  var isValidCheck = true;
  var isEqual = true;

  list1.forEach((x, i) => {
    if (isValidCheck && x !== list2[i]) {
      isValidCheck = false; isEqual = false;
    }
  })

  return isEqual;
}

export const IsSameValues = (list1, list2) => {
  var isValidCheck = true;
  var isSame = true;

  list1.forEach((x, i) => {
    if (isValidCheck && !list2.includes(x)) {
      isValidCheck = false; isSame = false;
    }
  })

  return isSame;
}

export const GroupBy = function (xs, key, gr_Prop) {
  var groups = [];

  xs.forEach(x => {
    var val = x[key];
    var group = undefined;
    var index = groups.findIndex(y => val === y.Key);
    if (index === -1) {
      group = {
        Key: val,
        Items: []
      };

      if (gr_Prop){
        group[gr_Prop] = val;
      }

      groups.push(group);
    } else {
      group = groups[index];
    }
    group.Items.push(x);
  })

  return groups;
};

export const Remove = (dict, key) => {
  var index = dict.findIndex(x => x.Key === key);
  if (index !== 0) {
    return dict.splice(index, 1);
  }
}

export const Add = (dict, func, isDelete = true) => {
  var key = newGUID();

  var newfunc = () => {
    func();
    if (isDelete) {
      Remove(dict, key);
    }
  }

  dict.push({
    Key: key,
    Value: newfunc
  });
}

export const Add_2 = (dict, func, isDelete = true) => {
  dict.push({
    Key: newGUID(),
    Value: func,
    IsDelete : isDelete
  });
}

export const Handle = (dict, newDict_Handle) => {
  dict.forEach(x => x.Value());

  dict = dict.filter(x => !x.IsDelete);
  newDict_Handle(dict);
}

// Truy xut thng tin Excel
export const ExcelColumnName = (n) => {
  var ordA = 'A'.charCodeAt(0);
  var ordZ = 'Z'.charCodeAt(0);
  var len = ordZ - ordA + 1;

  var s = "";
  while(n >= 0) {
      s = String.fromCharCode(n % len + ordA) + s;
      n = Math.floor(n / len) - 1;
  }
  return s;
}

export const ExcelCellName = (row, col) => {
  return `${ExcelColumnName(col)}${row+1}`;
}

export const ExcelRangeName = (row1, col1, row2, col2) => {
  return `${ExcelCellName(row1, col1)}:${ExcelCellName(row2, col2)}`
}

// Max min
export const MinMax = (arr) => {
  return arr.reduce(({min, max}, v) => ({
    min: min < v ? min : v,
    max: max > v ? max : v,
  }), { min: arr[0], max: arr[0] });
}

// Save Database
export const SaveDatabase = (q, url, handle) => {
  var apiData = q.ApiData
  var _id = apiData._id;

  var isNew = _id ? false : true
  const apiFunc = () => isNew ?
    api.post(`${url}/add`, apiData) :
    api.put(`${url}/edit`, apiData) 

  apiFunc().then(response => response.data)
    .then(data => {
      if (isNew){ 
        q._id = data._id
      }
      handle(data, isNew);
    })
}

export const DeleteDatabase = (q, url, handle) => {
  var apiData = {
    _id: q._id
  }

  const apiFunc = () => api.put(`${url}/delete`, apiData)

  apiFunc().then(response => response.data)
    .then(data => {
      handle(data);
    })
}

// Get plain text from paste
export const GetPlainText = (pasteEvent) => {
  var text = (pasteEvent.originalEvent || pasteEvent).clipboardData.getData('text/plain');
  return text;
}

// Remove new line
export const RemoveNewLine = (text) => {
  return text.replace(/\r?\n|\r/g, "");
}

// First - Last Item
export const GetFirst = (list) => {
  return list.length !== 0 ? list[0] : undefined;
}

export const GetLast = (list) => {
  return list.length !== 0 ? list[list.length -1] : undefined;
}

// Image
export const IsImage = (fileName) => {
  var isValid = false;
  var list = [ '.jpg', '.png', '.jpeg' ]

  list.forEach(x => {
    if (!isValid && fileName.includes(x)){
      isValid = true;
    }
  })

  return isValid;
}

export const SingleFilter = (list, filter, nullHandle) => {
  var index = list.findIndex(filter);
  var obj = index !== -1 ? list[index] : undefined

  if (obj === undefined && nullHandle) {
    var obj = nullHandle();
    list.push(obj);
  }

  return obj;
}