import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './BaseVMUtil'

import * as genUtil from '../../../../../util/generalUtil'

export class BaseVM {
  constructor() {
    makeObservable(this, {

    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // data
  fields
  get Fields() {
    if (!this.fields) {
      this.fields = util.GetFields(this)
    } return this.fields
  }

  GetField = (name) => {
    return genUtil.SingleFilter(this.Fields, x => x.Key === name);
  }

  // task
  taskDict
  get TaskDict(){
    if (!this.taskDict){
      this.taskDict = util.GetTaskDict(this)
    } return this.taskDict
  }

  // css
  get Css() {
    return this === this.Dict.CurrentItem ? 'bg-[#52c41a90] text-white font-medium' : ''
  }

  // current
  SetCurrent = () => {
    var dict = this.Dict

    if (dict.CurrentItem === this) {
      dict.CurrentItem = undefined
    } else {
      dict.CurrentItem = this
    }
  }

  // save
  Save = (handle) => {
    this.ModelItem.Save(handle);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  // validate
  Validate = (level) => {
    return this.Dict.Validate(level)
  }

  // view
  get TitleView() {
    return util.GetTitleView(this)
  }

  get ContentView(){
    return util.GetContentView(this)
  }
}