import { PMData } from './PMData';
import * as genUtil from '../../../../util/generalUtil'
import * as api from '../../../../util/apiUtil'

// user
import * as userDictUtil from '../../../user/dict/User_DictUtil'

// command
import * as commandDictUtil from '../../entity/user/command/dict/base/DictUtil'

// role
import * as roleDictUtil from '../../entity/user/role/dict/DictUtil'

// project
import * as projectDictUtil from '../../entity/project/dict/DictUtil'

// task
import * as taskDictUtil from '../../entity/task/base/dict/DictUtil'


// Instance
export const get = () => {
  var qI = new PMData();

  return qI;
}

// Property
// user
export const GetUserDict = (data) => {
  var baseUrl = 'https://api.bimdev.vn/api'
  
  var qI = userDictUtil.get(
    `${baseUrl}/bu_user`,
    data);

  // command render
  var cr_d = qI.CommandRender_Dict;
  cr_d.GetItem('admin').Render = (q, handle) => {
    var url = `${baseUrl}/bu_usercommand`

    var apiData = {
      userId : q.userId
    }

    api.post(`${url}/list`, apiData).then(res => res.data)
      .then(data => {
        if (data.length === 1){
          if (handle){
            handle(data[0].commands);
          }
        }
      })
  }

  // admin
  qI.Admin.AdminUser_Dict.Url = 'https://api.bimdev.vn/api/btr_userrole'

  return qI;
}

// role
export const GetRoleDict = (data) => {
  return roleDictUtil.get(data);
}

// Command
export const GetCommandDict = (q) => {
  var qI = commandDictUtil.get(q);
  return qI;
}

// project
export const GetProjectDict = (q) => {
  return projectDictUtil.get(q);
}

// task
export const GetTaskDict = (q) => {
  return taskDictUtil.get(q);
}