import { makeObservable, observable, action } from 'mobx';
import * as util from './DictUtil'
import * as itemUtil from '../base/BaseUtil'

import { Base as Task } from '../base/Base'

import * as genUtil from '../../../../../../util/generalUtil'
import { PMData } from '../../../../single/model/PMData';

export class Dict {
  constructor() {
    makeObservable(this, {
      items: observable,
    })
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var q = itemUtil.get(this)
    this.Items.push(q);

    return q
  }

  // 
  get IsInDict() {
    return this.Dict && !(this.Dict instanceof PMData)
  }

  get IsSub() {
    return this.Dict && this.Dict instanceof Task
  }
}