import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { DictVM } from './DictVM';

class View extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;


    return (
      <>
        {data.IsSub ? data.SubView : data.TableView}
      </>
    );
  }
}

export default observer(View);
