import { url } from './Base_Url'
import { Base } from './Base'
import * as subDictUtil from '../dict/DictUtil'
import * as statusUtil from '../status/StatusUtil'

import * as api from '../../../../../../util/apiUtil'

// Instance
export const GetFromData = (data, dict) => {
  var qI = get(dict);

  qI._id = data._id
  qI.Name = data.name
  qI.Is_ShowSub = data.isShowSub
  qI.Status.State = data.state

  return qI;
}

export const get = (dict) => {
  var qI = new Base();

  qI.Dict = dict;
  
  return qI;
}

// Property
// sub
export const GetSubDict = (q) => {
  return subDictUtil.get(q);
}

// status
export const GetStatus = (q) => {
  return statusUtil.get(q)
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}