import { BaseVM } from './BaseVM'
import { EntDelete } from '../../../../delete/EntDelete';
import * as f from '../field/Field'
import * as fieldUtil from '../../../../field/FieldUtil'
import * as taskDictUtil from '../../task/base/dict/DictVMUtil'

import * as inputUtil from '../../../../../util/inputUtil'
import { PMDataVM } from '../../../single/form/PMDataVM';
import { Switch } from 'antd';

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new BaseVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
// field
export const GetFields = (q) => {
  var inputs = f.List();

  var host = q.ModelItem;
  var fields = []

  inputs.forEach((x, i) => {
    var fName = f.GetName(x);

    var qI;
    qI = fieldUtil.get(
      () => host[fName],
      (v) => {
        host[fName] = v
      }, q.Save
    )

    // qI.PreReady = () => q.Validate('edit') && pmDataVM().ViewMode === 'edit'
    // qI.PreEdit = () => q.Validate('edit')

    qI.PreReady = () => pmDataVM().ViewMode === 'edit'

    qI.Key = x;
    fields.push(qI)
  })

  return fields;
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

// checklist
export const GetTaskDict = (q) => {
  return taskDictUtil.get(q.ModelItem.TaskDict, q)
}


// view
export const GetTitleView = (q) => {
  return <div key={q.id} className={`flex items-center ${q.Css}`}>
    <div className='w-6 my-2 h-5 cursor-pointer bg-[#52c41a90]'
      onClick={q.SetCurrent}>
    </div>

    <div className='mx-1'>
      {
      // q.Validate('edit') && 
      (pmDataVM().ViewMode === 'edit' || q.GetField(f.name).OnEdit) ?
        q.DeleteVM.View : ''}
    </div>

    <div className='flex-1 flex items-center'>
      <div className='mx-2'>
        {`${q.Index + 1}.`}
      </div>

      <div className='flex-1'>
        {q.GetField(f.name).WrapperView}
      </div>
    </div>
  </div>
}

export const GetContentView = (q) => {
  return <>
    <div key={q.id}>
      <div className=''>
        {q.TaskDict.View}
      </div>
    </div>
  </>
}