import { Body } from './Body';

import * as genUtil from '../../../../../util/generalUtil'
import * as comUtil from '../../../../../util/commandUtil'

import { convertToRaw, EditorState } from 'draft-js';
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as vt from '../../view/ViewType'
import { message, Switch } from 'antd';
import { PMData } from '../../../single/model/PMData';
import * as vm from '../../view/ViewMode'

const pmDataVM = () => PMDataVM.Instance
const pmData = () => PMData.Instance

// Instance
export const get = (dict) => {
  var qI = new Body();

  qI.Dict = dict

  return qI;
}


// Property
// view
export const GetContentView = (q) => {
  var qI = pmDataVM();

  switch (qI.RouteData[0]) {
    case vt.home:
      return <div className='mt-4'>
        {qI.ProjectDict.View}
      </div>
    case vt.checklist:
      return qI.CheckListDict.View
    case vt.role:
      return <div>
        <div>
          {qI.RoleDict.View}
        </div>

        <div className='mt-4'>
          {qI.UserDict.Admin.AdminUser_Dict.View}
        </div>
      </div>
  }
}

// main
export const GetMainView = (q) => {
  return !pmData().IsPublish ? <div className='m-10'>
    {GetContentView(q)}
  </div>
    : GetContentView(q)
}