import * as util from './PMDataUtil'
import * as genUtil from '../../../../util/generalUtil'
import * as vm from '../../entity/view/ViewMode'

export class PMData {
  constructor() {

  }

  static instance
  static get Instance() {
    if (!this.instance) {
      this.instance = util.get();
    }
    return this.instance;
  } static set Instance(v) {
    this.instance = v;
  }

  // 
  IsPublish = false

  // view
  ViewMode = vm.view

  // Data
  // user
  userDict
  get UserDict(){
    if (!this.userDict){
      this.userDict = util.GetUserDict(this)
    } return this.userDict
  }

  // role
  roleDict
  get RoleDict(){
    if (!this.roleDict){
      this.roleDict = util.GetRoleDict(this)
    } return this.roleDict
  }

  // Command
  commandDict
  get CommandDict(){
    if (!this.commandDict){
      this.commandDict = util.GetCommandDict(this)
    } return this.commandDict
  }

  // discipline
  projectDict
  get ProjectDict(){
    if (!this.projectDict){
      this.projectDict = util.GetProjectDict(this)
    } return this.projectDict
  }

  // task
  taskDict
  get TaskDict(){
    if (!this.taskDict){
      this.taskDict = util.GetTaskDict(this)
    } return this.taskDict
  }
}
