import { makeObservable, observable, action } from 'mobx';
import * as util from './DictUtil'
import * as itemUtil from '../base/BaseUtil'

import * as genUtil from '../../../../../util/generalUtil'

export class Dict {
  constructor() {
    makeObservable(this, {
      items: observable,
    })
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
    
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var q = itemUtil.get(this)
    this.Items.push(q);

    return q
  }
}