import { CloseCircleOutlined } from '@ant-design/icons';
import { BaseVM } from './BaseVM'
import { EntDelete } from '../../../../../delete/EntDelete';
import View from './BaseView'
import * as f from '../field/Field'
import * as fieldUtil from '../../../../../field/FieldUtil'
import * as subDictUtil from '../dict/DictVMUtil'

import * as statusUtil from '../status/StatusVMUtil'

import * as inputUtil from '../../../../../../util/inputUtil'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import { Switch } from 'antd';

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new BaseVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
// field
export const GetFields = (q) => {
  var inputs = f.List();

  var host = q.ModelItem;
  var fields = []

  inputs.forEach((x, i) => {
    var fName = f.GetName(x);

    var qI;
    qI = fieldUtil.get(
      () => host[fName],
      (v) => {
        host[fName] = v
      },
      q.Save,
      'html'
    )

    // qI.PreReady = () => q.Validate('edit') && pmDataVM().ViewMode === 'edit'
    // qI.PreEdit = () => q.Validate('edit')

    qI.PreReady = () => pmDataVM().ViewMode === 'edit'

    qI.Key = x;
    fields.push(qI)
  })

  return fields;
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.Retrieve_ShowHover_Button = () => {
    return <div className={`ml-2 cursor-pointer text-[#ff4d4f]`}
      onClick={qI.Show_Hover}>
      <CloseCircleOutlined />
    </div>
  }

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

// item

// sub
export const GetSubDict = (q) => {
  return subDictUtil.get(q.ModelItem.SubDict, q);
}

// rank
export const GetRank = (q) => {
  var supRank = q.Dict.Dict.Rank
  return supRank !== undefined ? supRank + 1 : 0
}

// status
export const GetStatus = (q) => {
  return statusUtil.get(q.ModelItem.Status, q)
}

// view
export const GetView = (q) => {
  return <View key={q.id} store={q} />
}

export const GetIndent_View = (q) => {
  var width = `${q.Rank * 17}px`

  return <div style={{ width }} />
}

export const GetRowView = (q) => {
  return <tr>
    <td className='non-input'>
      {q.Rank === 0 ? q.Index + 1 : ''}
    </td>
    <td className='non-input'>{q.Status.View}</td>
    <td>
      <div className='flex'>
        {q.Indent_View}
        <div className={`flex-1 ${q.Status.FullState === 'done' ? 'text-[#52c41a]' : ''}`}>
          {q.GetField(f.name).WrapperView}
        </div>
      </div>
    </td>
  </tr>
}